.dark-nav {
  padding: 20px;
  font-family: "Cairo";
  color: #ffffff;
  height: auto;
  background-color: transparent !important;
}

@media (min-width: 250px) and (max-width: 960px) {
  .dark-nav {
    background-color: white !important;
  }
}

.dark-nav .nav-brand img {
  width: 100.4px;
  height: 106.98px;
}

.dark-nav .navbar-toggler {
  background-color: white;
}

.dark-nav .navbar-toggler .navbar-toggler-icon {
  color: #ffffff !important;
}

.dark-nav .nav-container div:first-of-type .menu {
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.dark-nav .nav-container div:first-of-type .menu ul .nav-item {
  margin: 5px 0;
  cursor: pointer;
}

.dark-nav .nav-container div:first-of-type .menu ul .nav-item .nav-link {
  color: #000;
}

@media (min-width: 960px) {
  .dark-nav .nav-container div:first-of-type .menu ul .nav-item .nav-link {
    color: #fff;
  }
}

.dark-nav .nav-container .more {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 48%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  justify-self: flex-end;
}

@media (min-width: 250px) and (max-width: 960px) {
  .dark-nav .nav-container .more {
    width: 100%;
    color: #c89b2c !important;
  }
}

.dark-nav .nav-container .more div {
  margin: 5px 0;
  text-align: left;
}

.dark-nav .nav-container .more div span {
  margin-right: 20px;
}

@media (min-width: 250px) and (max-width: 960px) {
  .dark-nav .nav-container .more div span {
    margin-right: 10px;
  }
}

.dark-nav .nav-container .more div span i {
  margin-right: 5px;
}

@media (min-width: 250px) and (max-width: 960px) {
  .dark-nav .nav-container .more div span i {
    margin-right: 3px;
  }
}

.dark-nav .nav-container .more div button {
  border-radius: 5px;
  color: #fff !important;
  width: 35%;
  padding: 5px;
  margin: 0 5px;
  border: 1px solid #000;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.dark-nav .nav-container .more div button:first-of-type {
  border: 1px solid #c89b2c !important;
  background-color: #c89b2c !important;
}

.dark-nav .nav-container .more div button:first-of-type:hover {
  border: 1px solid #c89b2c !important;
  color: #c89b2c !important;
  background-color: transparent;
}

.dark-nav .nav-container .more div button:last-of-type {
  border: 1px solid #fff;
  background-color: transparent;
}

@media (min-width: 250px) and (max-width: 960px) {
  .dark-nav .nav-container .more div button:last-of-type {
    background-color: white !important;
    color: #c89b2c !important;
    border-color: #c89b2c !important;
  }
}

.dark-nav .nav-container .more div button:last-of-type:hover {
  border: 1px solid #c89b2c !important;
  background-color: #c89b2c !important;
}

@media (min-width: 250px) and (max-width: 960px) {
  .dark-nav .nav-container .more div button:last-of-type:hover {
    color: #fff !important;
  }
}
