.awards {
  margin-top: 2%;
  margin-bottom: 5%;
  width: 100% !important;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  margin-bottom: 200px !important;
}

.awards .awards-bg {
  background-image: url("./img/bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  height: 450px;
}

@media (min-width: 250px) and (max-width: 768px) {
  .awards .awards-bg {
    height: auto;
  }
}

.awards .awards-bg .awards-overlay {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  padding: 20px 15px;
  background: -webkit-gradient(linear, left top, right top, from(#876413d4), to(#4a1714d4));
  background: linear-gradient(to right, #876413d4, #4a1714d4);
}

.awards .awards-bg .awards-overlay .awards-content {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: white;
}

@media (min-width: 250px) and (max-width: 768px) {
  .awards .awards-bg .awards-overlay .awards-content {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
}

.awards .awards-bg .awards-overlay .awards-content .awards-latest {
  width: 10%;
  height: 200px;
  padding: 20px;
  border-radius: 20px;
  background-color: transparent;
  text-align: center;
  position: relative;
}

@media (min-width: 250px) and (max-width: 768px) {
  .awards .awards-bg .awards-overlay .awards-content .awards-latest {
    width: 100%;
    height: 100px;
  }
}

.awards .awards-bg .awards-overlay .awards-content .awards-latest h2 {
  width: 15%;
  line-height: 1.5;
}

@media (min-width: 250px) and (max-width: 768px) {
  .awards .awards-bg .awards-overlay .awards-content .awards-latest h2 {
    width: 100%;
  }
}

.awards .awards-bg .awards-overlay .awards-content .awards-latest:before {
  content: "";
  position: absolute;
  bottom: 10px;
  right: 20px;
  width: 30%;
  height: 5.5px;
  background-color: #c89b2c;
  border-radius: 20px;
}

@media (min-width: 250px) and (max-width: 768px) {
  .awards .awards-bg .awards-overlay .awards-content .awards-latest:before {
    right: 30%;
    width: 20%;
  }
}

.awards .awards-bg .awards-overlay .awards-content .awards-cards-parent {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  width: 75%;
}

.awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards-text {
  height: auto;
  -ms-flex-item-align: start;
      align-self: flex-start;
  justify-self: flex-start;
  margin-top: 20%;
}

.awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards-text h2 {
  margin-bottom: 3%;
}

.awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards-text p {
  width: 68%;
}

@media (min-width: 250px) and (max-width: 768px) {
  .awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards-text p {
    width: 100%;
  }
}

.awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards {
  margin-top: 5%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

@media (min-width: 250px) and (max-width: 960px) {
  .awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }
}

.awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards .awards-card {
  background-color: #fff;
  color: black;
  padding: 20px;
  border-radius: 5px;
  outline: 5px solid #fff;
  outline-offset: 8px;
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-shadow: 1px 1px 10px #d2d2d2;
          box-shadow: 1px 1px 10px #d2d2d2;
}

@media (min-width: 250px) and (max-width: 768px) {
  .awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards .awards-card {
    width: 65%;
    height: 300px;
    margin-bottom: 40px;
  }
}

.awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards .awards-card .card-img {
  height: 100px;
  margin-bottom: 15px;
}

.awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards .awards-card .card-img img {
  width: 100px;
}

.awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards .awards-card .card-text {
  padding: 10px 5px 2px;
  width: 150px;
  border-radius: 100% 100% 20% 20%;
}

.awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards .awards-card .card-text h4 {
  color: #4a1714;
  font-weight: bold;
}

.awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards .awards-card .card-text p {
  font-size: 14px;
}

.awards .awards-bg .awards-overlay .awards-content .awards-cards-parent .awards-cards .awards-card:first-child .card-text {
