.contact {
  margin-top: 50px !important;
  height: 500px !important;
  @media (min-width: 350px) and (max-width: 960px) {
    height: auto !important;
  }
  .card {
    h3 {
      width: 105px !important;
    }
  }
  .row-lay {
    .contact-all {
      flex-direction: row;
      justify-content: space-around;

      height: 500px !important;
      @media (min-width: 350px) and (max-width: 960px) {
        height: auto !important;
        flex-direction: column;
      }
      .map {
        width: 50% !important;
        @media (min-width: 350px) and (max-width: 960px) {
          width: 100% !important;
        }
      }
    }
  }
}
.mapouter {
  position: relative;
  text-align: right;
  height: 420px;
  width: 420px;
  @media (min-width: 350px) and (max-width: 960px) {
    width: 100%;
    margin-top: 15px;
  }
}
.gmap_canvas {
  overflow: hidden;
  background: none !important;
  height: 420px;
  width: 420px;
  @media (min-width: 350px) and (max-width: 960px) {
    width: 100%;
  }
  iframe {
    @media (min-width: 350px) and (max-width: 960px) {
      width: 100%;
    }
  }
}
