.news {
  margin-bottom: 10% !important;
  .card:first-child {
    h3 {
      width: 115px;
    }
  }
  .card:not(:first-child) {
    width: 24.5%;
    padding: 0;
    border-radius: 20px;
    height: auto !important;
    @media (min-width: 250px) and (max-width: 960px) {
      width: 85%;
      margin: 5% 0;
    }
    &:hover {
      transform: scale(1.1);
    }
    .card-img {
      flex-direction: column;
      img {
        width: 100%;
      }
      p {
        font-size: 12px;
        padding: 10px 10px;
        color: #c89b2c;
        align-self: flex-start;
      }
    }
    .card-text {
      margin-top: -25px;
      padding: 15px 10px 0;
      font-size: 14px;
      text-align: right;
    }
  }
}
