.light {
  padding: 20px;
  background-color: #fff;
  font-family: "Cairo";
  color: #000;
  height: auto;
  .nav-brand img {
    width: 100.4px;
    height: 106.98px;
  }
  .nav-container {
    div {
      &:first-of-type {
        .menu {
          justify-content: flex-end;
          align-self: flex-end;
          ul {
            .nav-item {
              cursor: pointer;
            }
            &.light {
              a {
                color: #000 !important;
              }
            }
          }
        }
      }
    }
    .more {
      display: flex;
      flex-direction: column;
      justify-self: flex-end;
      color: #c89b2c;
      width: 48%;
      @media (min-width: 250px) and (max-width: 768px) {
        width: 100%;
        color: #c89b2c;
      }
      div {
        span {
          margin-right: 20px;
          @media (min-width: 250px) and (max-width: 768px) {
            margin-right: 10px;
          }
          i {
            margin-right: 5px;

            @media (min-width: 250px) and (max-width: 768px) {
              margin-right: 3px;
            }
          }
        }
        margin: 5px 0;
        text-align: left;
        button {
          border-radius: 5px;
          color: #fff;
          width: 35%;
          padding: 5px;
          margin: 0 5px;
          border: 1px solid #000;
          transition: all 0.5s;
          &:first-of-type {
            border: 1px solid #c89b2c;
            background-color: #c89b2c;
            &:hover {
              border: 1px solid #c89b2c;
              color: #c89b2c;
              background-color: transparent;
            }
          }
          &:last-of-type {
            border: 1px solid #c89b2c;
            color: #c89b2c;
            background-color: transparent;
            &:hover {
              border: 1px solid #c89b2c;
              color: #fff;
              background-color: #c89b2c;
            }
          }
        }
      }
    }
  }
}
