.footer {
  padding: 30px 10px;
  background-color: #131d24;
  color: #fff;
  p {
    width: 100%;
    font-size: 13px;
    margin-top: 15px;
    small {
      display: block;
      color: #7c878e;
      font-weight: bold;
      font-size: 12px;
      direction: ltr;
      text-align: right;
      padding: 10px 0;
    }
  }
  .footer-head {
    padding: 15px;
    h5 {
      position: relative;
      padding: 15px 0;
      font-size: 20px;
      &:after {
        content: "";
        position: absolute;
        bottom: 0px;
        right: 0;
        width: 25%;
        border-radius: 10px;
        height: 5px;
        background-color: #c89b2c;
      }
    }
  }
  .footer-list {
    padding: 15px;
    ul {
      padding: 0;
      li {
        padding: 7px 0;
        a {
          color: #fff;
          text-decoration: none;
          display: flex;
          justify-content: right;
          flex-direction: row;
          align-items: center;
          i {
            width: 10%;
            color: #c89b2c;
            text-align: right;
          }
        }
      }
    }
  }
}
