.hero {
  padding: 10px;
  height: 80.1% !important;
  .hero-container {
    display: flex !important;
    flex-direction: row-reverse !important;
    justify-content: center;
    align-items: center;
    height: 94% !important;
    .hero-text {
      height: 300px;
      color: #fff !important;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      .text {
        justify-content: center;
        align-items: center;
        h3 {
          font-size: 40px;
        }
        p {
          text-align: center;
          margin: 5% auto;
          font-size: 24px;
          width: 85%;
        }
      }
    }
  }
  .hero-nav {
    align-self: center;
    justify-self: center;
    display: flex;
    flex-direction: column;
    button {
      all: unset;
      margin: 5px 0;
      border: 3px solid #fff;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      &.edit {
        border-radius: 20px;
        background-color: #fff;
        height: 50px;
      }
    }
  }
  .hero-scroll {
    border: 1px solid #fff;
    width: 30px;
    height: 60px;
    border-radius: 20px;
    position: relative;
    bottom: 10px;
    right: 50%;
    transform: translate(10px);
    &::before {
      content: " ";
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background-color: #fff;
      position: absolute;
      left: 50%;
      top: 15%;
      transform: translate(-50%) !important;
      animation: moving 1s ease-in-out 1s infinite alternate;
    }
  }
}
@keyframes moving {
  0% {
    top: 15%;
  }
  100% {
    top: 60%;
  }
}
