.contact-form {
  width: 35%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: #f9f9f9;
  padding: 20px 15px;
  border-radius: 10px;
}

@media (min-width: 350px) and (max-width: 960px) {
  .contact-form {
    width: 100%;
  }
}

.contact-form div {
  margin-top: 10px;
  text-align: right;
  font-size: 12px;
}

.contact-form div input,
.contact-form div textarea,
.contact-form div button {
  width: 100%;
  margin-top: 5px;
}

.contact-form div input {
  border: none;
  outline: none;
  -webkit-box-shadow: 1px 1px 5px #ddd, -1px -1px 5px #ddd;
          box-shadow: 1px 1px 5px #ddd, -1px -1px 5px #ddd;
  background-color: #fff;
}

.contact-form div input::-webkit-input-placeholder,
.contact-form div textarea::-webkit-input-placeholder {
  font-size: 12px;
}

.contact-form div input:-ms-input-placeholder,
.contact-form div textarea:-ms-input-placeholder {
  font-size: 12px;
}

.contact-form div input::-ms-input-placeholder,
.contact-form div textarea::-ms-input-placeholder {
  font-size: 12px;
}

.contact-form div input::placeholder,
.contact-form div textarea::placeholder {
  font-size: 12px;
}

.contact-form button {
  outline: none;
  border: none;
  display: block;
  margin-top: 10px;
  font-size: 14px;
  width: 45%;
  background-color: #c89b2c;
}
