.info {
  height: auto;
  display: block;
  margin: 10% 0;
  .container {
    height: 100%;
    position: relative;
    @media (min-width: 250px) and (max-width: 960px) {
      position: static;
    }
    .know {
      border-radius: 20px;
      width: 60%;
      height: 380px;
      background-size: 100% 100%;
      background-image: url("./img/NoPath.jpg");
      @media (min-width: 250px) and (max-width: 960px) {
        width: 100%;
      }
      .overlay {
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        padding: 20px;
        border-radius: 20px;
        background: linear-gradient(to right, #876413d4, #4a1714d4);
      }
      .title {
        padding: 5px;
        color: white;
        width: 130px;
        line-height: 1.5em;
        margin-right: 20px;
        height: 155px;
        position: relative;
        @media (min-width: 250px) and (max-width: 960px) {
          width: 27%;
        }
        h2 {
          line-height: 1.3;
        }
        &:before {
          position: absolute;
          content: "";
          width: 50%;
          height: 5px;
          bottom: 1px;
          background-color: #c89b2c;
          border-radius: 10px;
        }
      }
      .pic {
        width: 50%;
        text-align: center;
        img {
          width: 48%;
          @media (min-width: 250px) and (max-width: 960px) {
            width: 85%;
          }
        }
      }
    }
    .details {
      position: absolute;
      top: 13%;
      left: 30%;
      transform: translate(-50%);
      border-radius: 15px;
      background-color: #fff;
      padding: 20px;
      height: max-content;
      width: 45%;
      border-left: 1px solid #ccc;
      @media (min-width: 250px) and (max-width: 960px) {
        position: static;
        width: 100%;
        transform: translate(0%);
        margin: 15px 0;
        box-shadow: -1px -1px 5px #ccc, 1px 1px 5px #ccc;
      }
      .details-text {
        p {
          font-size: 16px;
        }
      }
      .cards {
        .card {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 95px;
          border-radius: 20px;
          margin: 0 5px;
          padding: 15px !important;
          max-height: 100px;
          .card-img {
            text-align: center;
            height: 50px;
            img {
              margin: 0 auto;
              width: 55px;
              height: 55px;
            }
          }
        }
      }
    }
  }
}
