.news {
  margin-bottom: 10% !important;
}

.news .card:first-child h3 {
  width: 115px;
}

.news .card:not(:first-child) {
  width: 24.5%;
  padding: 0;
  border-radius: 20px;
  height: auto !important;
}

@media (min-width: 250px) and (max-width: 960px) {
  .news .card:not(:first-child) {
    width: 85%;
    margin: 5% 0;
  }
}

.news .card:not(:first-child):hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.news .card:not(:first-child) .card-img {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.news .card:not(:first-child) .card-img img {
  width: 100%;
}

.news .card:not(:first-child) .card-img p {
  font-size: 12px;
  padding: 10px 10px;
  color: #c89b2c;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.news .card:not(:first-child) .card-text {
  margin-top: -25px;
  padding: 15px 10px 0;
  font-size: 14px;
  text-align: right;
}
