.dark-nav {
  padding: 20px;
  font-family: "Cairo";
  color: #ffffff;
  height: auto;
  background-color: transparent !important;
  @media (min-width: 250px) and (max-width: 960px) {
    background-color: white !important;
  }
  .nav-brand img {
    width: 100.4px;
    height: 106.98px;
  }
  .navbar-toggler {
    background-color: white;
    .navbar-toggler-icon {
      color: #ffffff !important;
    }
  }
  .nav-container {
    div {
      &:first-of-type {
        .menu {
          justify-content: flex-end;
          align-self: flex-end;
          ul {
            .nav-item {
              margin: 5px 0;
              cursor: pointer;
              .nav-link {
                color: #000;
                @media (min-width: 960px) {
                  color: #fff;
                }
              }
            }
          }
        }
      }
    }
    .more {
      display: flex;
      width: 48%;
      flex-direction: column;
      justify-self: flex-end;
      @media (min-width: 250px) and (max-width: 960px) {
        width: 100%;
        color: #c89b2c !important;
      }
      div {
        span {
          margin-right: 20px;
          @media (min-width: 250px) and (max-width: 960px) {
            margin-right: 10px;
          }
          i {
            margin-right: 5px;

            @media (min-width: 250px) and (max-width: 960px) {
              margin-right: 3px;
            }
          }
        }
        margin: 5px 0;
        text-align: left;
        button {
          border-radius: 5px;
          color: #fff !important;
          width: 35%;
          padding: 5px;
          margin: 0 5px;
          border: 1px solid #000;
          transition: all 0.5s;
          &:first-of-type {
            border: 1px solid #c89b2c !important;
            background-color: #c89b2c !important;
            &:hover {
              border: 1px solid #c89b2c !important;
              color: #c89b2c !important;
              background-color: transparent;
            }
          }
          &:last-of-type {
            border: 1px solid #fff;
            background-color: transparent;
            @media (min-width: 250px) and (max-width: 960px) {
              background-color: white !important;
              color: #c89b2c !important;
              border-color: #c89b2c !important;
            }
            &:hover {
              border: 1px solid #c89b2c !important;
              background-color: #c89b2c !important;
              @media (min-width: 250px) and (max-width: 960px) {
                color: #fff !important;
              }
            }
          }
        }
      }
    }
  }
}
