.hero {
  padding: 10px;
  height: 80.1% !important;
}

.hero .hero-container {
  display: -webkit-box !important;
  display: -ms-flexbox !important;
  display: flex !important;
  -webkit-box-orient: horizontal !important;
  -webkit-box-direction: reverse !important;
      -ms-flex-direction: row-reverse !important;
          flex-direction: row-reverse !important;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 94% !important;
}

.hero .hero-container .hero-text {
  height: 300px;
  color: #fff !important;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.hero .hero-container .hero-text .text {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.hero .hero-container .hero-text .text h3 {
  font-size: 40px;
}

.hero .hero-container .hero-text .text p {
  text-align: center;
  margin: 5% auto;
  font-size: 24px;
  width: 85%;
}

.hero .hero-nav {
  -ms-flex-item-align: center;
      align-self: center;
  justify-self: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.hero .hero-nav button {
  all: unset;
  margin: 5px 0;
  border: 3px solid #fff;
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

.hero .hero-nav button.edit {
  border-radius: 20px;
  background-color: #fff;
  height: 50px;
}

.hero .hero-scroll {
  border: 1px solid #fff;
  width: 30px;
  height: 60px;
  border-radius: 20px;
  position: relative;
  bottom: 10px;
  right: 50%;
  -webkit-transform: translate(10px);
          transform: translate(10px);
}

.hero .hero-scroll::before {
  content: " ";
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #fff;
  position: absolute;
  left: 50%;
  top: 15%;
  -webkit-transform: translate(-50%) !important;
          transform: translate(-50%) !important;
  -webkit-animation: moving 1s ease-in-out 1s infinite alternate;
          animation: moving 1s ease-in-out 1s infinite alternate;
}

@-webkit-keyframes moving {
  0% {
    top: 15%;
  }
  100% {
    top: 60%;
  }
}

@keyframes moving {
  0% {
    top: 15%;
  }
  100% {
    top: 60%;
  }
}
