.header {
  width: 100% !important;
  height: 100vh;
  padding: 0;
}

.header .header-content {
  padding: 0;
  width: 100% !important;
  height: 100%;
  background-image: url("./bg.jpg");
  background-size: 100% 100%;
}

.header .header-content .data {
  width: 100%;
  height: 100%;
  background: -webkit-gradient(linear, left top, right top, from(#4a1714a3), to(#87641389));
  background: linear-gradient(to right, #4a1714a3, #87641389);
}
