.light {
  padding: 20px;
  background-color: #fff;
  font-family: "Cairo";
  color: #000;
  height: auto;
}

.light .nav-brand img {
  width: 100.4px;
  height: 106.98px;
}

.light .nav-container div:first-of-type .menu {
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.light .nav-container div:first-of-type .menu ul .nav-item {
  cursor: pointer;
}

.light .nav-container div:first-of-type .menu ul.light a {
  color: #000 !important;
}

.light .nav-container .more {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  justify-self: flex-end;
  color: #c89b2c;
  width: 48%;
}

@media (min-width: 250px) and (max-width: 768px) {
  .light .nav-container .more {
    width: 100%;
    color: #c89b2c;
  }
}

.light .nav-container .more div {
  margin: 5px 0;
  text-align: left;
}

.light .nav-container .more div span {
  margin-right: 20px;
}

@media (min-width: 250px) and (max-width: 768px) {
  .light .nav-container .more div span {
    margin-right: 10px;
  }
}

.light .nav-container .more div span i {
  margin-right: 5px;
}

@media (min-width: 250px) and (max-width: 768px) {
  .light .nav-container .more div span i {
    margin-right: 3px;
  }
}

.light .nav-container .more div button {
  border-radius: 5px;
  color: #fff;
  width: 35%;
  padding: 5px;
  margin: 0 5px;
  border: 1px solid #000;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.light .nav-container .more div button:first-of-type {
  border: 1px solid #c89b2c;
  background-color: #c89b2c;
}

.light .nav-container .more div button:first-of-type:hover {
  border: 1px solid #c89b2c;
  color: #c89b2c;
  background-color: transparent;
}

.light .nav-container .more div button:last-of-type {
  border: 1px solid #c89b2c;
  color: #c89b2c;
  background-color: transparent;
}

.light .nav-container .more div button:last-of-type:hover {
  border: 1px solid #c89b2c;
  color: #fff;
  background-color: #c89b2c;
}
