.questions {
  .row-lay {
    .quest-all {
      .quest {
        width: 100%;
        text-align: right;
        border-radius: 15px;
        box-shadow: 1px 1px 10px #ddd, -1px -1px 10px #ddd;
        padding: 13px 15px;
        margin-bottom: 15px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        details {
          summary {
            list-style: none;
          }
          p {
            padding: 10px;
          }
        }

        button {
          all: unset;
          font-size: 20px;
        }
      }
    }
  }
}
