.services {
  height: 600px;
  margin-top: 5%;
}

@media (min-width: 350px) and (max-width: 960px) {
  .services {
    height: auto;
  }
}

.services .row {
  margin: 25px 0;
  color: #4a1714;
}

.services .row h2 {
  font-size: 40px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px;
  position: relative !important;
}

.services .row h2:after {
  content: " ";
  position: absolute;
  bottom: 0;
  right: 10px;
  width: 20%;
  height: 5px;
  border-radius: 20px;
  background-color: #c89b2c;
}

.services .row.serv {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.services .row.serv .item {
  border-radius: 40px;
  border: 1px solid #ccc;
  height: 300px;
  margin-top: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.services .row.serv .item a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  text-decoration: none;
  color: #4a1714;
}

.services .row.serv .item a .head {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 20px;
  border-radius: 25px;
  width: 45%;
  background-color: #c89b2c;
}

.services .row.serv .item a .head.company {
  background-color: #2c98c8;
}

.services .row.serv .item a .head.ind {
  background-color: #2cc87a;
}

.services .row.serv .item a .title {
  margin: 25px 0 0 0;
}

.services .row.serv .item a .title h3 {
  font-size: 18px;
}
