.agent {
  height: 80s0px;
  margin: 4% 0;
  margin-bottom: 5%;
  @media (min-width: 350px) and (max-width: 960px) {
    height: auto;
  }
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .agent-title {
    width: 30%;
    color: #4a1714;
    margin-bottom: 50px;
    @media (min-width: 350px) and (max-width: 960px) {
      width: 90%;
    }
    h3 {
      position: relative;
      font-size: 45px;
      padding: 10px 0;

      &::after {
        content: "";
        position: absolute;
        bottom: -10px;
        right: 0;
        width: 15%;
        border-radius: 20px;
        height: 8px;
        background-color: #c89b2c;
      }
    }
  }
  .agent-form {
    width: 28%;
    border: 1px solid #ccc;
    height: 400px;
    border-radius: 20px;
    padding: 20px;
    @media (min-width: 350px) and (max-width: 960px) {
      width: 90%;
    }
    display: flex;
    align-items: center;
    justify-content: center;
    form {
      margin: 0 auto;
      width: 80%;
      color: #4a1714;
      input {
        margin: 15px 0;
        padding: 15px 0;
        border-radius: 10px;
        text-indent: 4px;
      }
      .form-forget {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        margin-top: -10px;
        margin-bottom: 10px;
        font-size: 14px;
        div {
          display: flex;
          font-weight: 400;
          justify-content: center;
          align-items: flex-start;
          label {
            margin-right: -5px;
            display: flex;
            flex-direction: row;
            align-items: center;
          }
        }
        p {
          margin-top: 10px;
        }
      }
      button {
        all: unset;
        color: #fff;
        text-align: center;
        display: block;
        width: 95%;
        padding: 10px;
        background-color: #4a1714;
        &.newagent {
          background-color: #fff;
          color: #000;
          span {
            color: #c89b2c;
          }
        }
      }
    }
  }
}
