.layout {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  @media (min-width: 350px) and (max-width: 960px) {
    flex-direction: column;
  }
  .card {
    height: 300px;
    width: 235px;
    margin-left: 10px;
    margin-right: 10px;
    padding: 20px 15px;
    border: none;
    border-radius: 10px;
    transition: all 0.5s;
    z-index: 11;

    @media (min-width: 350px) and (max-width: 960px) {
      margin-top: 10%;
    }
    &:not(:first-child) {
      box-shadow: 1px 1px 5px #ccc, 1px 1px 10px #ccc;

      &:hover {
        transform: scale(1.2);
        z-index: 11 !important;
      }
    }

    h3 {
      text-align: right;
      width: 152px;
      font-size: 38px;
      position: relative;
      &:before {
        content: "";
        position: absolute;
        width: 75px;
        border-radius: 20px;
        height: 5px;
        background-color: #c89b2c;
        bottom: -35px;
      }
    }
    &:first-child button {
      all: unset;
      cursor: pointer;
      border-radius: 5px;
      height: 30px;
      padding: 5px;
      margin-top: 60px;
      border: 1px solid #4a1714;
      color: #4a1714;
      display: flex;
      flex-direction: row;
      align-items: center;
      flex-wrap: wrap;

      justify-content: space-between;
      transition: all 0.5s;
      span {
        text-align: center;
        flex-basis: 120px;
        font-size: 18px;
      }
      i {
        color: #fff;
        flex-basis: 10px;
        padding: 8px;
        transition: all 0.5s;
        background-color: #4a1714;
      }
      &:hover {
        color: white;
        background-color: #4a1714;
        i {
          color: #4a1714;
          background-color: #fff;
        }
      }
    }
    .card-img {
      height: 200px;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        width: 164px;
        height: 164px;
        &.big {
          width: 115px;
          height: 115px;
        }
      }
    }
    .card-text {
      margin: 1.5% 0;
      font-size: 18px;
      text-overflow: hidden;
      overflow: hidden;
    }
    button {
      all: unset;
      margin: 0 auto;
      width: max-content;
      cursor: pointer;
      i {
        color: #c89b2c;
        flex-basis: 10px;
        border: 1px solid #c89b2c;
        padding: 8px;
        border-radius: 5px;
        transition: all 0.5s;
        background-color: white;
      }
      &:hover i {
        color: #fff;
        background-color: #c89b2c;
      }
    }
  }
}
.lay-two {
  height: 400px !important;
  @media (min-width: 350px) and (max-width: 960px) {
    height: auto !important;
    margin-top: -100px !important;
  }
  .row-lay {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 100% !important;
    .all {
      width: 80% !important;
      border-radius: 20px;
      padding: 20px 15px;
      border: 1px solid #ccc;
      height: 400px !important;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      @media (min-width: 350px) and (max-width: 960px) {
        height: auto !important;
        width: 100% !important;
      }
    }
  }
}
