.awards {
  margin: {
    top: 2%;
    bottom: 5%;
  }
  width: 100% !important;
  height: max-content;
  margin-bottom: 200px !important;
  .awards-bg {
    background-image: url("./img/bg.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    height: 450px;
    @media (min-width: 250px) and (max-width: 768px) {
      height: auto;
    }
    .awards-overlay {
      width: 100%;
      height: 100%;
      border-radius: 20px;
      padding: 20px 15px;
      background: linear-gradient(to right, #876413d4, #4a1714d4);
      .awards-content {
        height: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-content: center;
        justify-content: space-around;
        align-items: center;
        @media (min-width: 250px) and (max-width: 768px) {
          flex-direction: column;
        }
        color: white;
        .awards-latest {
          width: 10%;
          @media (min-width: 250px) and (max-width: 768px) {
            width: 100%;
            height: 100px;
          }
          height: 200px;
          padding: 20px;
          border-radius: 20px;
          background-color: transparent;
          text-align: center;
          position: relative;
          h2 {
            width: 15%;
            line-height: 1.5;
            @media (min-width: 250px) and (max-width: 768px) {
              width: 100%;
            }
          }
          &:before {
            content: "";
            position: absolute;
            bottom: 10px;
            right: 20px;
            width: 30%;
            height: 5.5px;
            background-color: #c89b2c;
            border-radius: 20px;
            @media (min-width: 250px) and (max-width: 768px) {
              right: 30%;
              width: 20%;
            }
          }
        }
        .awards-cards-parent {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: flex-start;
          width: 75%;
          .awards-cards-text {
            height: auto;
            align-self: flex-start;
            justify-self: flex-start;
            margin-top: 20%;

            h2 {
              margin: {
                bottom: 3%;
              }
            }
            p {
              width: 68%;
              @media (min-width: 250px) and (max-width: 768px) {
                width: 100%;
              }
            }
          }
          .awards-cards {
            margin-top: 5%;
            display: flex;
            width: 100%;
            flex-direction: row;
            justify-content: space-around;

            @media (min-width: 250px) and (max-width: 960px) {
              flex-direction: column;
              justify-content: center;
              align-items: center;
            }
            .awards-card {
              background-color: #fff;
              color: black;
              padding: 20px;
              border-radius: 5px;
              outline: 5px solid #fff;
              outline-offset: 8px;
              text-align: center;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              box-shadow: 1px 1px 10px rgb(210, 210, 210);
              @media (min-width: 250px) and (max-width: 768px) {
                width: 65%;
                height: 300px;
                margin-bottom: 40px;
              }
              .card-img {
                height: 100px;
                margin-bottom: 15px;
                img {
                  width: 100px;
                }
              }
              .card-text {
                padding: 10px 5px 2px;
                width: 150px;
                border-radius: 100% 100% 20% 20%;
                h4 {
                  color: #4a1714;
                  font-weight: bold;
                }
                p {
                  font-size: 14px;
                }
              }
              &:first-child .card-text {
                background-color: #ddd;
              }
            }
          }
        }
      }
    }
  }
}
