.footer {
  padding: 30px 10px;
  background-color: #131d24;
  color: #fff;
}

.footer p {
  width: 100%;
  font-size: 13px;
  margin-top: 15px;
}

.footer p small {
  display: block;
  color: #7c878e;
  font-weight: bold;
  font-size: 12px;
  direction: ltr;
  text-align: right;
  padding: 10px 0;
}

.footer .footer-head {
  padding: 15px;
}

.footer .footer-head h5 {
  position: relative;
  padding: 15px 0;
  font-size: 20px;
}

.footer .footer-head h5:after {
  content: "";
  position: absolute;
  bottom: 0px;
  right: 0;
  width: 25%;
  border-radius: 10px;
  height: 5px;
  background-color: #c89b2c;
}

.footer .footer-list {
  padding: 15px;
}

.footer .footer-list ul {
  padding: 0;
}

.footer .footer-list ul li {
  padding: 7px 0;
}

.footer .footer-list ul li a {
  color: #fff;
  text-decoration: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: right;
      -ms-flex-pack: right;
          justify-content: right;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.footer .footer-list ul li a i {
  width: 10%;
  color: #c89b2c;
  text-align: right;
}
