.info {
  height: auto;
  display: block;
  margin: 10% 0;
}

.info .container {
  height: 100%;
  position: relative;
}

@media (min-width: 250px) and (max-width: 960px) {
  .info .container {
    position: static;
  }
}

.info .container .know {
  border-radius: 20px;
  width: 60%;
  height: 380px;
  background-size: 100% 100%;
  background-image: url("./img/NoPath.jpg");
}

@media (min-width: 250px) and (max-width: 960px) {
  .info .container .know {
    width: 100%;
  }
}

.info .container .know .overlay {
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 20px;
  border-radius: 20px;
  background: -webkit-gradient(linear, left top, right top, from(#876413d4), to(#4a1714d4));
  background: linear-gradient(to right, #876413d4, #4a1714d4);
}

.info .container .know .title {
  padding: 5px;
  color: white;
  width: 130px;
  line-height: 1.5em;
  margin-right: 20px;
  height: 155px;
  position: relative;
}

@media (min-width: 250px) and (max-width: 960px) {
  .info .container .know .title {
    width: 27%;
  }
}

.info .container .know .title h2 {
  line-height: 1.3;
}

.info .container .know .title:before {
  position: absolute;
  content: "";
  width: 50%;
  height: 5px;
  bottom: 1px;
  background-color: #c89b2c;
  border-radius: 10px;
}

.info .container .know .pic {
  width: 50%;
  text-align: center;
}

.info .container .know .pic img {
  width: 48%;
}

@media (min-width: 250px) and (max-width: 960px) {
  .info .container .know .pic img {
    width: 85%;
  }
}

.info .container .details {
  position: absolute;
  top: 13%;
  left: 30%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  border-radius: 15px;
  background-color: #fff;
  padding: 20px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  width: 45%;
  border-left: 1px solid #ccc;
}

@media (min-width: 250px) and (max-width: 960px) {
  .info .container .details {
    position: static;
    width: 100%;
    -webkit-transform: translate(0%);
            transform: translate(0%);
    margin: 15px 0;
    -webkit-box-shadow: -1px -1px 5px #ccc, 1px 1px 5px #ccc;
            box-shadow: -1px -1px 5px #ccc, 1px 1px 5px #ccc;
  }
}

.info .container .details .details-text p {
  font-size: 16px;
}

.info .container .details .cards .card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 95px;
  border-radius: 20px;
  margin: 0 5px;
  padding: 15px !important;
  max-height: 100px;
}

.info .container .details .cards .card .card-img {
  text-align: center;
  height: 50px;
}

.info .container .details .cards .card .card-img img {
  margin: 0 auto;
  width: 55px;
  height: 55px;
}
