.contact-form {
  width: 35%;
  height: fit-content;
  background-color: #f9f9f9;
  padding: 20px 15px;
  border-radius: 10px;
  @media (min-width: 350px) and (max-width: 960px) {
    width: 100%;
  }
  div {
    margin-top: 10px;
    text-align: right;
    font-size: 12px;
    input,
    textarea,
    button {
      width: 100%;
      margin-top: 5px;
    }
    input {
      border: none;
      outline: none;
      box-shadow: 1px 1px 5px #ddd, -1px -1px 5px #ddd;
      background-color: #fff;
    }
    input::placeholder,
    textarea::placeholder {
      font-size: 12px;
    }
  }
  button {
    outline: none;
    border: none;
    display: block;
    margin-top: 10px;
    font-size: 14px;
    width: 45%;
    background-color: #c89b2c;
  }
}
