.services {
  height: 600px;
  margin-top: 5%;
  @media (min-width: 350px) and (max-width: 960px) {
    height: auto;
  }
  .row {
    margin: 25px 0;
    color: #4a1714;
    h2 {
      font-size: 40px;
      width: fit-content;
      padding: 10px;
      position: relative !important;
      &:after {
        content: " ";
        position: absolute;
        bottom: 0;
        right: 10px;
        width: 20%;
        height: 5px;
        border-radius: 20px;
        background-color: #c89b2c;
      }
    }

    &.serv {
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      .item {
        border-radius: 40px;
        border: 1px solid #ccc;
        height: 300px;
        margin-top: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        a {
          display: flex;
          align-items: center;
          flex-direction: column;
          justify-content: center;
          width: 100%;
          text-decoration: none;
          color: #4a1714;
          .head {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 20px;
            border-radius: 25px;
            width: 45%;
            background-color: #c89b2c;
            &.company {
              background-color: #2c98c8;
            }
            &.ind {
              background-color: #2cc87a;
            }
          }

          .title {
            margin: 25px 0 0 0;
            h3 {
              font-size: 18px;
            }
          }
        }
      }
    }
  }
}
